<template>
  <view class="container">
    <view class="form-section">
      <view class="section-title">ÂÆùÂÆù‰ø°ÊÅØ</view>
      
      <view class="form-item">
        <view class="form-label">ÂßìÊ∞è</view>
        <input 
          class="form-input" 
          v-model="babyInfo.surname" 
          placeholder="ËØ∑ËæìÂÖ•ÂÆùÂÆùÂßìÊ∞è"
        />
      </view>
      
      <view class="form-item">
        <view class="form-label">ÊÄßÂà´</view>
        <view class="gender-selector">
          <view 
            class="gender-item" 
            :class="{ active: babyInfo.gender === 'male' }"
            @click="selectGender('male')"
          >
            üë¶ Áî∑Â≠©
          </view>
          <view 
            class="gender-item" 
            :class="{ active: babyInfo.gender === 'female' }"
            @click="selectGender('female')"
          >
            üëß Â•≥Â≠©
          </view>
        </view>
      </view>
      
      <view class="form-item">
        <view class="form-label">Âá∫ÁîüÊó•Êúü</view>
        <picker 
          mode="date" 
          :value="babyInfo.birthDate" 
          @change="onDateChange"
        >
          <view class="picker-item">
            {{ babyInfo.birthDate || 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó•Êúü' }}
          </view>
        </picker>
      </view>
      
      <view class="form-item">
        <view class="form-label">Âá∫ÁîüÊó∂Èó¥</view>
        <picker 
          mode="time" 
          :value="babyInfo.birthTime" 
          @change="onTimeChange"
        >
          <view class="picker-item">
            {{ babyInfo.birthTime || 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó∂Èó¥' }}
          </view>
        </picker>
      </view>
      
      <view class="form-item">
        <view class="form-label">Ëµ∑ÂêçÂÅèÂ•Ω</view>
        <view class="preference-list">
          <view 
            v-for="(pref, index) in preferences" 
            :key="index"
            class="preference-item"
            :class="{ active: babyInfo.preferences.includes(pref.value) }"
            @click="togglePreference(pref.value)"
          >
            {{ pref.label }}
          </view>
        </view>
      </view>
      
      <button class="btn-primary" @click="generateNames" :disabled="loading">
        {{ loading ? 'Ê≠£Âú®ÁîüÊàê...' : 'ÂºÄÂßãËµ∑Âêç' }}
      </button>
    </view>
  </view>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { nameApi } from '../../api/user.js'

const loading = ref(false)

const babyInfo = reactive({
  surname: '',
  gender: '',
  birthDate: '',
  birthTime: '',
  preferences: []
})

const preferences = ref([
  { label: 'ÂØìÊÑèÁæéÂ•Ω', value: 'good_meaning' },
  { label: 'ÊúóÊúó‰∏äÂè£', value: 'easy_pronounce' },
  { label: 'ÊñáÈõÖËØóÊÑè', value: 'poetic' },
  { label: 'ÂØìÊÑèÊô∫ÊÖß', value: 'wisdom' },
  { label: 'ÂØìÊÑèÂÅ•Â∫∑', value: 'health' },
  { label: 'ÂØìÊÑèÂØåË¥µ', value: 'wealth' }
])

const selectGender = (gender) => {
  babyInfo.gender = gender
}

const onDateChange = (e) => {
  babyInfo.birthDate = e.detail.value
}

const onTimeChange = (e) => {
  babyInfo.birthTime = e.detail.value
}

const togglePreference = (value) => {
  const index = babyInfo.preferences.indexOf(value)
  if (index > -1) {
    babyInfo.preferences.splice(index, 1)
  } else {
    babyInfo.preferences.push(value)
  }
}

const generateNames = async () => {
  // È™åËØÅÂøÖÂ°´Â≠óÊÆµ
  if (!babyInfo.surname) {
    uni.showToast({
      title: 'ËØ∑ËæìÂÖ•ÂßìÊ∞è',
      icon: 'error'
    })
    return
  }
  
  if (!babyInfo.gender) {
    uni.showToast({
      title: 'ËØ∑ÈÄâÊã©ÊÄßÂà´',
      icon: 'error'
    })
    return
  }
  
  if (!babyInfo.birthDate) {
    uni.showToast({
      title: 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó•Êúü',
      icon: 'error'
    })
    return
  }
  
  loading.value = true
  
  try {
    const result = await nameApi.generateNames(babyInfo)
    
    if (result.success) {
      // Â∞ÜÁªìÊûú‰øùÂ≠òÂà∞È°µÈù¢ÂèÇÊï∞‰∏≠ÔºåË∑≥ËΩ¨Âà∞ÁªìÊûúÈ°µ
      uni.navigateTo({
        url: `/pages/name-result/index?names=${encodeURIComponent(JSON.stringify(result.data))}`
      })
    } else {
      uni.showToast({
        title: result.message || 'Ëµ∑ÂêçÂ§±Ë¥•',
        icon: 'error'
      })
    }
  } catch (error) {
    console.error('Ëµ∑ÂêçÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑ÈáçËØï',
      icon: 'error'
    })
  } finally {
    loading.value = false
  }
}
    
// ‰∏çÈúÄË¶Å returnÔºåsetup ËØ≠Ê≥ïÁ≥ñ‰ºöËá™Âä®Êö¥Èú≤
</script>

